#cfw-payment-request-buttons .wc-stripe-banner-checkout.active {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    padding: 0;
    max-width: 100% !important;
    margin-inline-end: 0 !important;

    .banner-title {
        display: none;
    }

    ul.wc_stripe_checkout_banner_gateways {
        display: flex;

        .gpay-card-info-container {
            min-width: 140px;
        }

        .wc-stripe-checkout-banner-gateway {
            overflow: hidden;
        }
    }

    ul.wc_stripe_checkout_banner_gateways {
        margin-inline-end: 0px !important;

        li.wc-stripe-checkout-banner-gateway {

            > div {
                @include cfw-payment-request-button();
            }
        }
    }

    li.wc-stripe-checkout-banner-gateway {
        button {
            max-height: 42px !important;
            min-height: 42px !important;
            height: 42px !important;
            margin-inline-end: 0 !important;
        }
    }
}

main.checkoutwc {
    .gpay-button {
        background-repeat: no-repeat !important;
        background-position: center !important;
    }

    .wc-stripe-paymentRequest-icons-container {
        display: none;
    }
}