/**
 * Show / Hide Shipping Toggle
 *
 * Controlled by cfw-hide-shipping body class
 */
#cfw-customer-info .cfw-continue-to-payment-btn {
    display: none !important;
}

#cfw-payment-method .cfw-return-to-information-btn {
    display: none !important;
}

.cfw-hide-shipping {
    #cfw-breadcrumb li.cfw-shipping-method,
    #cfw-shipping-method,
    .cfw-review-pane li.shipping-method,
    .cfw-continue-to-shipping-btn,
    .cfw-return-to-shipping-btn {
        display: none !important;
        opacity: 0;
    }

    #cfw-customer-info .cfw-continue-to-payment-btn {
        display: block !important;
    }

    #cfw-payment-method .cfw-return-to-information-btn {
        display: block !important;
    }
}

.cfw_cart_item_after_data {
    margin-top: 0.5em;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .cfw-edit-item-quantity-control-wrap {
        margin-inline-end: 7px;
    }
}

/**
 * One Page Checkout
 */
.cfw-one-page-checkout .cfw-panel {
    opacity: 1 !important;
    display: block !important;
}

// We can't rely on the existing style, as it uses the `active` class which is not present in OPC.
// So we always want to use the last div within order_review in case of customizations or changes to structure.
.cfw-one-page-checkout #order_review > div:last-of-type {
    padding-bottom: 4em;
}

