// Native Login Form
.add_gift_card_form {
    margin-top: 1em;

    .woocommerce-info, .woocommerce-error, .woocommerce-message {
        padding: 10px;
        margin-bottom: 1em;
        border: 1px solid #dfe3e8;
        border-radius: 5px;
        line-height: 1.625;

        color: #383d41;
        background-color: #fafafa;
    }

    .woocommerce-error {
        color: #721c24;
        background-color: #f8d7da;
        border-color: #f5c6cb;
    }
}

#wc_gc_cart_code_field .optional {
    display: none;
}